import axios from 'axios'
const baseURL = '/players'

const getAllService = () => {
    const req = axios.get(baseURL)
    return req.then(res => res.data)
}

const createService = obj => {
    const req = axios.post(baseURL, obj)
    return req.then(res => res.data)
}

const deleteService = id => {
    const req = axios.delete(`${baseURL}/${id}`)
    return req.then(res => res.data)
}

const updateService = (obj, id) => {
    const req = axios.put(`${baseURL}/${id}`, obj)
    return req.then(res => res.data)
}

const toggleService = (obj, id) => {
    const req = axios.patch(`${baseURL}/${id}`, obj)
    return req.then(res => res.data)
}

export default {getAllService, createService, deleteService, updateService, toggleService}